
export default {
    formatNum(num){
        if (num>10000) {
            let reg = /(?=(\B)(\d{3})+$)/g;
            let m = parseInt(num/10000) + ''
            return m.replace(reg,',') + ""
        } else {
            return num;
        }
    },
    formatSeconds(s){
        let secondTime = parseInt(s);// 
        let minuteTime = 0;// 
        if(secondTime > 60) {//60
            //60
            minuteTime = parseInt(secondTime / 60);
            //
            secondTime = parseInt(secondTime % 60);
            //60
        }
        
        secondTime = secondTime < 10 ? "0" + secondTime : secondTime;
        minuteTime = minuteTime < 10 ? "0" + minuteTime : minuteTime;

        return minuteTime + ":" + secondTime;
    }
}
